<template>
<section>    
    <tg-list :IsShowAction="false" :listType="['List']" ModuleName="Asset" SubModuleCode="Asset" IdentityColumn="feature_id" :ListData="ModuleFeatureList" :HeaderData="Headers" :widgets="widgets" :showCheckBox="false" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :RenderRowActionMethod="renderActions" ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="sortExp" :SortBy="sortBy" v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata" :NorecordfoundText="$t('NoRecordfound')">
        <template v-slot:slotdata="{ data }">
            <template v-if="data.column.COLUMN_NAME == 'Action'">
                <em class="fa fa-pencil text-success action-icon center-icon" v-on:click="Edit(data.row)" :title="data.column.COLUMN_NAME"></em>
            </template>
        </template>
    </tg-list>
    <AddFeature v-if="IsAddFeature"></AddFeature>
</section>
</template>

    
<script>
import DataService from "../../service/DataService";

import AddFeature from '../../views/AdvancedSetup/AddFeature';

export default {
    components: {
        AddFeature
    },
    data() {
        return {
            ModuleFeatureList: [],
            IsAddFeature: false,
            sortBy: '',
            sortExp: '',
            TotalRecords: 0,
            pageNumber: 1,
            pageSize: 10,
            CurrentPage: 1,
            Headers: [{
                    "COLUMN_NAME": "module_name",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('moduleName'),
                    "DISPLAY_ORDER": 1,

                },
                {
                    "COLUMN_NAME": "sub_module_name",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('SubModuleName'),
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "feature_name",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Features'),
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "feature_based_on",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('BasedOn'),
                    "DISPLAY_ORDER": 4,

                },
                {
                    "COLUMN_NAME": "Action",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Action'),
                    "DISPLAY_ORDER": 5,
                    settings: {
                        isInSlot: true
                    }
                },
            ],
            listheaderbuttons: [{
                title: 'Add Feature',
                iconClass: 'fa fa-plus',
                callbackfunction: 'AddModule',
                additionalClass: '',
                isdisabled: false,
                isvisible: true,
                checkPrivilege: false
            }],
            widgets: [],
            LegendArray: [],
            totalrecords: null
        }
    },
    async created() {
        await this.FetchData();
    },
    methods: {
        AddModule: function () {
            this.IsAddFeature = true;
        },
        Edit: function () {
            this.IsAddFeature = true;
        },
        async FetchData() {
            var vueObj = this;
            vueObj.isLoading = true;
            var url = `module_id=&sub_module_id=&feature_id=&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&pageSize=${vueObj.pageSize}&pageNumber=${vueObj.pageNumber}`;
            await DataService.GetAdvancedFeatureListing(url).then(response => {
                vueObj.isLoading = false;
                if (response.data != null && response.data != 0) {
                    if (response.data.length > 0) {
                        vueObj.ModuleFeatureList = response.data;
                        vueObj.TotalRecords = response.data[0].TOTAL;
                        vueObj.TotalPages = Math.ceil(
                            vueObj.TotalRecords / vueObj.pageSize
                        );
                        vueObj.CurrentPage = vueObj.pageNumber;
                        vueObj.noRecord = false;
                    } else {
                        vueObj.noRecord = true;
                        vueObj.ModuleFeatureList = [];
                        vueObj.TotalRecords = 0;
                    }
                } else {
                    vueObj.noRecord = true;
                    vueObj.ModuleFeatureList = [];
                    vueObj.TotalRecords = 0;
                }
                setTimeout(function () {
                    vueObj.CheckBoxBootstrap();
                }, 100);
            })

        },
        renderActions: function () {

        },
        pagerMethod: function (value) {
            this.pageSize = value.pageSize;
            this.pageNumber = value.pageNumber;
            this.FetchData();
        },       
       
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.sortBy = s;
            if (s === this.sortBy) {
                if (this.sortExp == "") {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.sortExp == "ASC") {
                    this.sortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + "  span:first-child").attr("class", SortDir);
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "AddModule":
                    this.AddModule();
                    break;
            }
        },
    }
}
</script>
